import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/inventory/itemTransfer/actions";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";

import { Modal, Tooltip, Table, Row, Col, Empty, Popconfirm } from "antd";
import { StatusTag, StatusBadge, TransNo } from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";
import NumberFormat from "../../../App/NumberFormat";

const { getItemTransfers, receiveItemTransfer } = actions;

const expandedRowRender = data => {
  const columns = [
    {
      title: getLanguage({ id: "no." }),
      dataIndex: "index_no",
      width: "5%"
      // fixed: "left"
    },
    {
      title: getLanguage({ id: "code" }),
      dataIndex: "item_code",
      width: "10%",
      // fixed: "left",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "itemName" }),
      dataIndex: "item_name",
      width: "15%",
      render: text => {
        if (text && text.length > 50) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 50) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "quantity" }),
      dataIndex: "qty",
      width: "10%",
      align: "center",
      render: text => <NumberFormat value={text} />
    },
    {
      title: getLanguage({ id: "unit" }),
      width: "10%",
      dataIndex: "unit.unit_code",
      align: "center"
    }
  ];

  const dataSource = data;
  return (
    <div>
      <strong>
        <IntlMessages id="purchaseInvoices.importPR.itemList" />
      </strong>

      <Table
        rowKey={record => record.index_no}
        size="small"
        columns={columns}
        dataSource={dataSource}
        pagination={true}
        style={{ marginTop: "24px" }}
      />
    </div>
  );
};

class ModalReceive extends Component {
  render() {
    // console.log(this.props);

    const { visible, handleModal, itemTransfer } = this.props;
    const { prevTransReceive } = itemTransfer ? itemTransfer : [];
    const item_transfers = prevTransReceive
      ? prevTransReceive.item_transfers
        ? prevTransReceive.item_transfers
        : []
      : [];
    const { loadingForeign } = itemTransfer ? itemTransfer : false;

    const columns = [
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "item_transfer_no",
        rowKey: "item_transfer_no",
        width: "30%",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "pending";
              break;
            case 2:
              className = "confirmed";
              break;
            case 3:
              className = "cancelled";
              break;
            case 4:
              className = "received";
              break;
            default:
              className = "pending";
              break;
          }
  
          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <TransNo>{text}</TransNo>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "15%",
        render: text => {
          if (text) {
            return <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>;
          }
        }
      },
      {
        title: getLanguage({ id: "fromLocation" }),
        dataIndex: "from_location_name",
        rowKey: "from_location_name",
        width: "30%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "toLocation" }),
        dataIndex: "to_location_name",
        rowKey: "to_location_name",
        width: "30%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                <a href="javascript:;">{text.substring(0, 25) + "..."}</a>
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status",
        rowKey: "status",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "pending";
              display = getLanguage({ id: "pending" });
              break;
            case 2:
              className = "confirmed";
              display = getLanguage({ id: "confirmed" });
              break;
            case 3:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            case 4:
              className = "received";
              display = getLanguage({ id: "received" });
              break;
            default:
              className = "pending";
              display = getLanguage({ id: "pending" });
              break;
          }
  
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: "",
        dataIndex: "id_item_transfer",
        rowKey: "id_item_transfer",
        width: "10%",
        align: "center",
        render: text => (
          <Popconfirm
            title={<IntlMessages id="confirmReceive" />}
            onConfirm={() => this.props.receiveItemTransfer(
              text,
              { received_at: moment().format("YYYY-MM-DD HH:mm:ss") }
            )}
            okText={<IntlMessages id="yes" />}
            cancelText={<IntlMessages id="no" />}
          >
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                !this.props.permissions.includes("Confirm Item Transfer")
                  ? true
                  : false
              }
              loading={this.props.itemTransfer.loadingForeign}
              size="small"
            >
              <IntlMessages id="button.receiveTransaction" />
            </Button>
          </Popconfirm>
        )
      }
    ];

    const dataSource = [];
    Object.keys(item_transfers).map((item, index) => {
      return dataSource.push({
        ...item_transfers[item],
        key: item
      });
    });

    return (
      <Modal
        title={<IntlMessages id="itemTransfers.receiveTransaction" />}
        visible={visible}
        centered
        footer={null}
        keyboard={true}
        onCancel={handleModal}
        width="95%"
        okText={getLanguage({ id: "button.ok" })}
        cancelText={getLanguage({ id: "button.cancel" })}
      >
        <Row>
          <Col>
            {dataSource.length === 0 && !loadingForeign ? (
              <Empty
                description={<IntlMessages id="itemTransfers.table.noData" />}
              />
            ) : (
              <Table
                dataSource={dataSource}
                columns={columns}
                loading={loadingForeign}
                expandedRowRender={record =>
                  expandedRowRender(record.item_transfer_details)
                }
                expandIcon={props => (
                  <Button
                    onClick={e => props.onExpand(props.record, e)}
                    size="small"
                  >
                    <IntlMessages id="button.view" />
                  </Button>
                )}
              />
            )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default compose(
  connect(
    null,
    { getItemTransfers, receiveItemTransfer }
  )
)(ModalReceive);
