const actions = {
  getItemTransfers: (page, search, filter) => ({
    type: "GET_ITEM_TRANSFERS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getItems: (id_location, search) => ({
    type: "GET_FILTER_ITEMS_IT_REQUEST",
    payload: { id_location: id_location, search: search }
  }),
  createItemTransfer: () => ({
    type: "CREATE_ITEM_TRANSFER_REQUEST"
  }),
  getTransactions: (page, search, filter) => ({
    type: "GET_TRANSACTIONS_IT_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  storeItemTransfer: data => ({
    type: "STORE_ITEM_TRANSFER_REQUEST",
    payload: { body: data }
  }),
  editItemTransfer: id_item_transfer => ({
    type: "EDIT_ITEM_TRANSFER_REQUEST",
    payload: { id_item_transfer: id_item_transfer }
  }),
  updateItemTransfer: (id_item_transfer, data) => ({
    type: "UPDATE_ITEM_TRANSFER_REQUEST",
    payload: {
      id_item_transfer: id_item_transfer,
      body: data
    }
  }),
  showItemTransfer: id_item_transfer => ({
    type: "SHOW_ITEM_TRANSFER_REQUEST",
    payload: { id_item_transfer: id_item_transfer }
  }),
  confirmItemTransfer: id_item_transfer => ({
    type: "CONFIRM_ITEM_TRANSFER_REQUEST",
    payload: { id_item_transfer }
  }),
  receiveItemTransfer: (id_item_transfer, data) => ({
    type: "RECEIVE_ITEM_TRANSFER_REQUEST",
    payload: { id_item_transfer, body: data }
  }),
  unreceiveItemTransfer: (id_item_transfer, data) => ({
    type: "UNRECEIVE_ITEM_TRANSFER_REQUEST",
    payload: { id_item_transfer: id_item_transfer, body: data }
  }),
  cancelItemTransfer: (id_item_transfer, data) => ({
    type: "CANCEL_ITEM_TRANSFER_REQUEST",
    payload: { id_item_transfer, body: data }
  }),
  uploadDocument: (id_item_transfer, data) => ({
    type: "UPLOAD_ITEM_TRANSFER_DOCUMENT_REQUEST",
    payload: {
      id_item_transfer,
      body: data
    }
  }),
  downloadDocument: id_item_transfer_document => ({
    type: "DOWNLOAD_ITEM_TRANSFER_DOCUMENT_REQUEST",
    payload: { id_item_transfer_document }
  }),
  deleteDocument: (id_item_transfer_document, id_item_transfer) => ({
    type: "DELETE_ITEM_TRANSFER_DOCUMENT_REQUEST",
    payload: {
      id_item_transfer_document: id_item_transfer_document,
      id_item_transfer: id_item_transfer
    }
  }),
  setPrintData: data => ({
    type: "SET_PRINT_DATA_ITEM_TRANSFER",
    payload: { body: data }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
